const defaultOptions = {
    el: '',              //æå°ç®æ domèç¹ï¼eg: this.refs.view
    debug: true,        //æå¼è°è¯æ¨¡å¼ï¼ä¼æ¾ç¤ºiframe,
    importCss: true,     //å¼å¥head ä¸­çlink stylesheet
    importStyle: true,   //å¼å¥styleæ ç­¾ä¸­çæ ·å¼
    loadCss: [],         //éè¦è½½å¥çç¬¬ä¸æ¹æ ·å¼è¡¨
    title: '',          //æå°æ é¢
    delay: 300,         //å»¶è¿æå°æ¶é´ï¼ç¡®ä¿iframeä¸­çéæèµæºå è½½å®æ
    beforePrinfHandle: null,  //æå¼æå°çªå£åçé©å­å½æ°,å¯ä»¥éå¯¹æå°ææ¡£è¿è¡èªå®ä¹è°æ´ï¼æ¥åä¸ä¸ªdocumentåæ°
    afterPrintHandle: null,   //æå°å®æçé©å­å½æ°,
}
let iframe = null
let dom = null
/**
 * æ£æ¥éç½®æ¯å¦åæ³
 * @param {*} options 
 */
const checkOptions = options => {
    if(!options.el) {
        throw new Error('el must be a nodeType')
    }
    return {
        ...defaultOptions,
        ...options
    }

}
const prinf = options => {
    const op = checkOptions(options)
    dom = op.el.cloneNode(true)
    const handle = createIframe(op)
    if(op.beforePrinfHandle) {
        op.beforePrinfHandle(handle.contentDocument);
    }
    if (op.afterPrintHandle) {
        op.afterPrintHandle();
    }
    setTimeout(() => {
        handle.print();
        if (op.debug === false) {
            removeIframe();
        }
    }, op.delay)
}

const createIframe = (op) => {
    const { debug, importCss, importStyle, loadCss, title} = op 
    removeIframe();
    iframe = document.createElement('iframe');
    if(debug === false) {
        iframe.style.display = 'none'
    }
    //æå¥éè¦æå°çç®æ åç´ 
    document.querySelector('body').appendChild(iframe)
    iframe.contentDocument.title = title;
    const { body, head } = iframe.contentDocument;
    const contentWindow = iframe.contentWindow;
    //æå¥headä¸­çlink stylesheet
    if(importCss) {
        const stylesheets = document.querySelectorAll("link[rel = 'stylesheet']")
        stylesheets.forEach(item => {
            head.appendChild(item.cloneNode(true))
        })
    }
    //æå¥style
    if (importStyle) {
        const stylesheets = document.querySelectorAll("style")
        stylesheets.forEach(item => {
            body.appendChild(item.cloneNode(true))
        })
    }
    //æå¥å¤é¨æ ·å¼æä»¶
    if (Array.isArray(loadCss) && loadCss.length > 0) {
        loadCss.forEach(item => {
            head.appendChild(item)
        })
    }
    body.appendChild(dom)
    return contentWindow;
}
const removeIframe = () => {
    if (iframe) {
        document.querySelector('body').removeChild(iframe)
        iframe = null
    }
}

export default prinf